.feedback-screen {
  padding: 20px 0 50px;
  &__container {}
  &__form-border {
    border-radius: 10px;
    background-color: @gray;
    padding: 10px;
  }
  &__form-inner {
    background-color: white;
    padding: 40px 15px;
    background-position: bottom 0 left -10000%;
    background-size: auto;
    background-repeat: no-repeat;
  }
  &__content-holder {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 380px;
    margin: 0 auto;
  }
  &__info {
    text-align: center;
    margin-bottom: 20px;
  }
  &__title {
    color: @dark-blue;
    font-size: 26px;
    .light();
    margin-bottom: 30px;
  }
  &__title-hl {
    .bold();
  }
  &__form-descr {
    color: @dark-blue;
    .regular();
    font-size: 14px;
    line-height: 1.57;
    letter-spacing: 0.35px;
    max-width: 350px;
  }
}

@media screen and (min-width: 375px) {
  .feedback-screen {
    &__form-inner {
      padding: 40px 34px;
    }
  }
}

@media screen and (min-width: 768px) {
  .feedback-screen {
    padding: 30px 0 60px;
    &__form-inner {
      padding: 50px 37px;
    }
    &__content-holder {
      max-width: none;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      flex-wrap: wrap;
    }
    &__info {
      width: 45%;
      text-align: left;
    }
    &__title {
      font-size: 26px;
      line-height: 1.43;
    }
    &__form-wrap {
      max-width: 265px;
      width: 45%;
    }
  }
}

@media screen and (min-width: 1200px) {
  .feedback-screen {
    padding: 40px 0 84px;
    &__form-inner {
      padding: 60px 65px 30px;
      background-position: bottom 0 left calc(~"50% + (188px / 2)");
    }
    &__info {
      width: 50%;
    }
    &__title {
      font-size: 30px;
      line-height: 1.5;
    }
    &__title-hl {
      display: block;
    }
  }
}