.quote-screen {
  padding: 54px 0 50px;
  &__bg-wrap {
    display: none;
  }
  &__quote-wrap {
    text-align: center;
    margin-bottom: 34px;
  }
  &__quote {
    color: @dark-blue;
    font-size: 14px;
    .light();
    line-height: 2;
    margin-bottom: 5px;
    &:last-child {
      margin-bottom: 0;
    }
    &_bold {
      .medium();
    }
  }
  &__button-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__button-wrap-text {
    text-align: center;
    color: @dark-blue;
    font-size: 13px;
    .medium();
    font-style: italic;
    line-height: 1.54;
    margin-bottom: 15px;
  }
  &__bottom {
    display: none;
  }
}

@media screen and (min-width: 375px) {
  .quote-screen {
    &__button-wrap {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      max-width: 345px;
      margin: 0 auto;
    }
    &__button-wrap-text {
      text-align: right;
      margin-bottom: 0;
      max-width: 160px;
    }
  }
}

@media screen and (min-width: 768px) {
  .quote-screen {
    padding: 60px 0 30px;
    position: relative;
    &__bg-wrap {
      display: block;
      position: absolute;
      overflow: hidden;
      width: 100%;
      top: 0;
      left: 0;
      height: 100%;
      z-index: 1;
    }
    &__bg-container {
      height: inherit;
      position: relative;
    }
    &__bg-img-wrap {
      position: absolute;
      bottom: 0;
      right: 16px;
      display: block;
      width: 251px;
    }
    &__bg-img {
      max-width: 100%;
      vertical-align: middle;
      object-fit: cover;
    }
    &__container {
      position: relative;
      z-index: 2;
    }
    &__content {
      max-width: calc(~"100% - 234px");
    }
    &__info {
      max-width: 450px;
    }
    &__quote-wrap {
      text-align: left;
    }
    &__quote {
      font-size: 20px;
      line-height: 1.5;
      margin-bottom: 5px;
    }
    &__button-wrap {
      justify-content: flex-end;
      max-width: none;
      margin-bottom: 60px;
    }
    &__button-wrap-text {
      line-height: 1.43;
      font-size: 14px;
      max-width: 175px;
      margin-right: 14px;
    }
    &__bottom {
      display: block;
      text-align: right;
    }
    &__name {
      color: @dark-blue;
      .medium();
      font-style: italic;
      line-height: 1.13;
      font-size: 14px;
      margin-bottom: 5px;
    }
    &__post {
      color: #c5ced5;
      .medium();
      font-size: 14px;
      font-style: italic;
      line-height: 1.13;
    }
  }
}

@media screen and (min-width: 1000px) {
  .quote-screen {
    padding-bottom: 43px;
    height: 595px;
    &__bg-img-wrap {
      width: 461px;
      right: -23px;
    }
    &__container {
      height: 100%;
    }
    &__content {
      padding-top: 100px;
      height: 100%;
      max-width: calc(~"100% - 424px");
      display: flex;
      flex-direction: column;
    }
    &__info {
      flex-grow: 20;
    }
  }
}


@media screen and (min-width: 1200px) {
  .quote-screen {
    &__info {
      max-width: 680px;
    }
    &__quote {
      font-size: 30px;
      line-height: 1.67;
      margin-bottom: 5px;
    }
  }
}