.advices-grid {
  display: grid;
  grid-template-columns: none;
  &__item {
    &_mini {
      display: none;
    }
    &_big {
      grid-column: 1;
      grid-row: 1;
    }
  }
}

@media screen and (min-width: 768px) {
  .advices-grid {
    grid-template-columns: calc(~"(100% / 2) - 10px");
    grid-gap: 20px;
    &__item {
      &_mini {
        display: block;
        grid-column: 1;
        grid-row: 1;
      }
      &:nth-child(2) {
        grid-column: 1;
        grid-row: 2;
      }
      &_big {
        grid-column: 2;
        grid-row: ~"1 / 3";
      }
    }
  }
}

@media screen and (min-width: 1200px) {
  .advices-grid {
    grid-template-columns: calc(~"36% - 10px") calc(~"64% - 10px");
  }
}