.hamburger {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
  padding: 12px;
  margin-right: -12px;
  cursor: pointer;
  &__text {
    color: #ffffff;
    .bold();
    font-size: 12px;
    line-height: 1;
    letter-spacing: 0.3px;
    margin-right: 10px;
  }
  &__icon-wrap {
    width: 20px;
    position: relative;

    .hamburger_close & {
      width: 22px;
      height: 22px;
    }
  }
  &__icon-line {
    width: 100%;
    height: 4px;
    background-color: white;
    margin-bottom: 4px;
    border-radius: 2px;

    .hamburger_close & {
      width: 28px;
      background-color: @dark-blue;
      margin-bottom: 0;
      position: absolute;
      height: 3px;
      top: 50%;
      left: -4px;
      margin-top: -3px / 2;
      transform-origin: center;

      &:first-child {
        transform: rotate(45deg);
      }
      &:last-child {
        transform: rotate(-45deg);
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}