.popup {
  padding: 0;
  background: none;
  width: 100%;

  &_std {
    max-width: 704px;
  }
  &_error {
    max-width: 636px;
  }
  &_success {
    max-width: 514px;
  }

  &__inner-wrap {
    padding: 40px 15px;
  }
  &__content-wrap {
    position: relative;
    background-color: white;
    border-radius: 8px;
    padding: 40px 20px 60px;
    width: 100%;

    .popup_centered-content & {
      text-align: center;
    }

    .popup_info & {
      padding: 40px 20px;
    }

    .popup_advice & {
      background: @gray url(../img/logo-pattern.svg) no-repeat top 20px right -57px;
      background-size: 150px;
    }

    .btn_short {
      display: inline-flex;
    }
  }
  &__close {
    cursor: pointer;
    background: transparent;
    border: 0;
    padding: 10px;
    position: absolute;
    bottom: 100%;
    right: 0;

    .icon {
      display: block;
      &.icon-menu-close {
        .svg-icon(icon-menu-close, 15px ,16px);
      }
    }
  }
  &__content {
    margin: 0 auto;
    max-width: 350px;

    .popup_info & {
      max-width: none;
    }
  }
  &__date {
    color: rgba(0, 22, 40, .5);
    .light();
    font-style: italic;
    font-size: 14px;
    line-height: 1.12;
    margin-bottom: 20px;
  }
  &__title {
    text-align: center;
    color: @dark-blue;
    .light();
    font-size: 25px;
    line-height: 1.2;
    margin-bottom: 15px;
  }
  &__sub-title {
    text-align: center;
    color: @dark-blue;
    .light();
    font-size: 14px;
    line-height: 1.12;
    margin-bottom: 25px;
  }
  &__logo {
    position: absolute;
    right: 20px;
    bottom: 20px;

    .icon-popup-logo {
      display: block;
      .svg-icon(icon-popup-logo, 31px, 20px);
    }
  }
}

@media screen and (min-width: 575px) {
  .popup {

    &__inner-wrap {
      padding: 40px;
    }
    &__content-wrap {
      padding: 70px 34px 60px;

      .popup_info & {
        padding: 40px 34px;
      }
    }
    &__content {
      max-width: 260px;

      .popup_info & {
        max-width: none;
      }
    }
    &__close {
      bottom: 100%;
      left: 100%;
      right: auto;
    }
    &__title {
      font-size: 30px;
    }
    &__sub-title {
      margin-bottom: 40px;
    }
    &__logo {
      right: 34px;
      bottom: 34px;

      .icon-popup-logo {
        display: block;
        .svg-icon(icon-popup-logo, 43px, 27px);
      }
    }
  }
}