.advantages {
  &__item {
    margin-bottom: 55px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__rate-wrap {
    width: 100px;
    height: 100%;
    position: relative;
    margin: 0 auto 30px;
  }
  &__rate {
    text-align: center;
    color: #ffffff;
    .medium();
    font-size: 22px;
    line-height: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &__svg {
    width: 100%;
    height: 100%;
  }
  &__descr {
    text-align: center;
    color: #ffffff;
    .regular();
    font-size: 14px;
    line-height: 1.57;
    max-width: 220px;
    margin: 0 auto;
  }
}


@media screen and (min-width: 850px) {
  .advantages {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    &__item {
      margin-bottom: 0;
    }
    &__rate-wrap {
      width: 120px;
      height: 120px;
      position: relative;
      margin-left: 0;
    }
    &__rate {
      font-size: 30px;
    }
    &__svg {
      width: 100%;
      height: 100%;
    }
    &__descr {
      text-align: left;
      line-height: 1.71;
    }
  }
}

@media screen and (min-width: 1000px) {
  .advantages {
    justify-content: flex-start;
    &__item {
      margin-right: 160px;
      &:last-child {
        margin-right: 0;
      }
    }
    &__rate-wrap {
      width: 120px;
      height: 120px;
      position: relative;
      margin-left: 0;
    }
    &__rate {
      font-size: 30px;
    }
    &__svg {
      width: 100%;
      height: 100%;
    }
    &__descr {
      text-align: left;
      line-height: 1.71;
    }
  }
}