.info-box {
  font-size: 14px;
  line-height: 1.43;
  color: @dark-blue;
  .regular();

  .advice_popup & {
    .light();
  }

  h1, h2, h3, h4, h5 {
    .semi-bold();
    line-height: 1.5;
  }

  h1 {
    font-size: 22px;
    margin-bottom: 25px;
  }
  h2 {
    font-size: 20px;
    margin-bottom: 15px;
  }
  h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  h4 {
    font-size: 16px;
    margin-bottom: 10px;
  }
  h5 {
    font-size: 14px;
    margin-bottom: 10px;
  }


  p {
    color: inherit;
    font-family: inherit;
    font-weight: inherit;
    font-size: inherit;
    line-height: 1.43;
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: @blue;
    font-family: inherit;
    font-weight: inherit;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }

  ul {
    margin-bottom: 20px;

    li {
      color: inherit;
      font-family: inherit;
      font-weight: inherit;
      padding-left: 20px;
      position: relative;
      margin-bottom: 11px;
      &:last-child {
        margin-bottom: 0;
      }
      &:before {
        content: '';
        position: absolute;
        top: 11px;
        left: 7px;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: @blue;
      }
    }
  }
  ol {
    margin-bottom: 20px;
    counter-reset: list1;
    li {
      color: inherit;
      font-family: inherit;
      font-weight: inherit;
      padding-left: 5px;
      position: relative;
      margin-bottom: 11px;
      list-style-type: none;
      &:before {
        content: counter(list1) ". ";
        counter-increment: list1;
        .semi-bold();
      }
    }
  }

  em, strong, b {
    .semi-bold();
  }
}

@media screen and (min-width: 575px) {
  .info-box {
    font-size: 16px;

    h1 {
      font-size: 25px;
    }
    h2 {
      font-size: 22px;
    }
    h3 {
      font-size: 20px;
    }
    h4 {
      font-size: 18px;
    }
    h5 {
      font-size: 16px;
    }


    p {
      margin-bottom: 15px;
    }
  }
}