.advice {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  background-color: @gray;
  transition: background-color .3s ease;
  &:hover {
    background-color: white;
    .advice__descr:after {
      box-shadow: inset 0 -10px 15px white;
    }
  }
  &_short {
    margin-bottom: 20px;
    border: 2px solid @gray;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &_popup {
    background-color: transparent;
    border-radius: 0;
    &:hover {
      background-color: transparent;
    }
  }
  &__link {
    text-decoration: none;
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__col {
    &_image {

      .advice_short & {
        display: none;
      }
    }
    &_info {
      background: url(../img/logo-pattern.svg) no-repeat top 20px right -57px;
      background-size: 148px;
      padding: 38px 20px 100px;
      border: 2px solid @gray;
      border-top: 0;
      border-radius: 0 0 10px 10px;
      flex-grow: 20;

      .advice_short & {
        border: 0;
      }
    }
  }
  &__image {
    width: 100%;
    height: 183px;
    object-fit: cover;
    vertical-align: middle;
  }
  &__date {
    color: rgba(0, 22, 40, .5);
    .light();
    font-style: italic;
    font-size: 14px;
    line-height: 1.12;
    margin-bottom: 20px;
  }
  &__title {
    color: @dark-blue;
    .medium();
    font-size: 16px;
    line-height: 1.38;
    margin-bottom: 24px;

    .advice_short & {
      width: 245px;
    }
  }
  &__descr {
    color: @dark-blue;
    .light();
    font-size: 14px;
    line-height: 1.57;
    height: 126px;
    overflow: hidden;
    position: relative;
    .advice_popup & {
      height: auto;
    }
    &:after {
      content: "";
      width: 100%;
      height: 15px;
      position: absolute;
      bottom: 0;
      left: 0;
      box-shadow: inset 0 -10px 15px @gray;
      transition: all .3s ease;

      .advice_popup & {
        display: none;
      }
    }
  }
}

@media screen and (min-width: 575px) {
  .advice {
    &__image {
      height: 253px;
    }
  }
}

@media screen and (min-width: 768px) {
  .advice {
    &__col {
      &_info {
        padding: 38px 20px;
      }
    }
    &__title {
      font-size: 18px;
      line-height: 1.22;
    }
    &__descr {
      .advice_short & {
        line-height: 1.43;
        height: 105px;
      }
    }
  }
}

@media screen and (min-width: 1200px) {
  .advice {
    &__link {
      height: 100%;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: stretch;
    }
    &__image {
      height: 100%;
    }
    &__col {
      &_image {
        width: 46%;
        flex-shrink: 0;
      }
      &_info {
        width: 54%;
        padding-left: 68px;
        padding-right: 68px;
        border: 2px solid @gray;
        border-left: 0;
        border-radius: 0 10px 10px 0;

        .advice_short & {
          width: 100%;
          padding-left: 40px;
          padding-right: 40px;
        }
      }
    }
    &__content {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    &__title {
      line-height: 1.67;
    }
    &__descr {
      .advice_short & {
        height: 84px;
      }
    }
  }
}