.menu {
  position: fixed;
  display: block;
  top: 0;
  right: 0;
  width: 100%;
  background-color: white;
  z-index: 30;
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
  &_is-opened {
    animation: menuShow 333ms cubic-bezier(.8,0,.6,1) both;
  }
  &_is-closed {
    animation: menuHide 333ms cubic-bezier(.8,0,.6,1) both;
  }

  &__inner {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
  }

  &__head {
    padding: 15px;
    border-bottom: 1px solid #cbd7df;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-shrink: 0;
  }
  &__logo-link {
    text-decoration: none;
  }
  &__logo {
    width: 126px;
    vertical-align: middle;
  }
  &__contacts {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .menu__head & {
      display: none;
    }

    .menu__footer & {
      display: flex;
    }
  }
  &__phone {
    color: @dark-blue;
    .light();
    font-size: 16px;
    text-decoration: none;
    margin-bottom: 20px;
    &_bold {
      .bold();
      margin-bottom: 0;
    }
  }
  &__hamburger-wrap {}
  &__body {
    flex-grow: 20;
    height: 100%;
  }
  &__body-inner {
    width: 100%;
    height: 100%;
  }
  &__body-scroll {
    padding: 30px 15px;
  }
  &__footer {
    padding-bottom: 40px;
  }
}

@keyframes menuShow {
  0% {
    visibility: hidden;
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
    visibility: visible;
  }
}

@keyframes menuHide {
  0% {
    opacity: 1;
    transform: translateX(0);
    visibility: visible;
  }
  100% {
    visibility: hidden;
    opacity: 0;
    transform: translateX(100%);
  }
}

@media screen and (min-width: 768px) {
  .menu {
    &__head {
      padding: 15px 40px;
    }
    &__contacts {
      flex-direction: row;

      .menu__head & {
        display: flex;
      }

      .menu__footer & {
        display: none;
      }

      .btn {
        margin-left: 20px;
      }
    }
    &__phone {
      margin-bottom: 0;
    }
    &__body-scroll {
      padding: 30px 15px;
    }
    &__footer {
      display: none;
    }
  }
}

@media screen and (min-width: 1000px) {
  .menu {
    height: 100%;
    width: 45%;
    max-width: 630px;
    &__head {
      padding: 20px 40px;
      align-items: flex-start;
      border-bottom: 0;
    }
    &__logo-link {
      display: none;
    }
    &__contacts {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;

      .btn {
        margin-left: 0;
      }
    }
    &__phone {
      font-size: 18px;
      margin-bottom: 15px;
      &_bold {
        margin-bottom: 0;
      }
    }
    &__body {
      flex-grow: 20;
      min-height: 250px;
      height: 100%;
      overflow: hidden;
    }
    &__body-inner {
      width: calc(~"100% + 50px");
      height: 100%;
      overflow-y: scroll;
    }
    &__body-scroll {
      padding: 20px 40px;
    }
    &__footer {
      flex-shrink: 0;
      position: relative;
      display: block;
      padding: 30px 40px 20px;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 40px;
        width: calc(~"100% - 80px");
        height: 6px;
        border-radius: 3px;
        background-color: @gray;
      }
    }
  }
}

@media screen and (min-width: 1200px) {
  .menu {
    &__head {}
    &__contacts {
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;

      .btn {
        margin-left: 20px;
      }
    }
    &__phone {
      margin-bottom: 0;
    }
  }
}

@media screen and (min-width: 1350px) {
  .menu {
    &__head {
      padding: 20px 40px 20px 80px;
    }
    &__body-scroll {
      padding: 85px 40px 85px 80px;
    }
    &__phone {
      font-size: 20px;
    }
    &__footer {
      padding: 85px 40px 85px 80px;
      &:before {
        left: 80px;
        width: calc(~"100% - (80px + 50px)");
      }
    }
  }
}

@media (max-height: 600px) and (min-width: 1350px) {
  .menu {
    &__body-scroll {
      padding-top: 20px;
      padding-bottom: 20px;
    }
    &__footer {
      padding-top: 30px;
      padding-bottom: 20px;
    }
  }
}
