.processes-screen {
  padding: 50px 0 60px;
  background-color: @dark-blue;
  position: relative;
  &__bg {
    display: none;
  }
  &__container {
    position: relative;
  }
  &__content {
    padding-top: 40px;
    text-align: center;

    .btn {
      display: inline-flex;
    }
  }
  &__title {
    color: #ffffff;
    .light();
    font-size: 26px;
    line-height: 1.2;
    letter-spacing: -1.3px;
    margin-bottom: 30px;
  }
  &__descr {
    color: #ffffff;
    .light();
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: 0.35px;
    padding: 0 15px;
    height: 184px;
    overflow: hidden;
    max-width: 490px;
    margin: 0 auto 40px;
    position: relative;
    &:after {
      content: "";
      width: 100%;
      height: 15px;
      position: absolute;
      bottom: 0;
      left: 0;
      box-shadow: inset 0px -10px 15px @dark-blue;
    }
  }
}

@media screen and (min-width: 575px) {
  .processes-screen {
    &__descr {
      height: 115px;
    }
  }
}

@media screen and (min-width: 768px) {
  .processes-screen {
    &__title {
      font-size: 28px;
      letter-spacing: -1.4px;
    }
  }
}

@media screen and (min-width: 1000px) {
  .processes-screen {
    padding: 110px 0 120px;
    background-color: @dark-blue;
    &__container {}
    &__content {
      padding-top: 75px;
      text-align: left;
      max-width: 840px;
    }
    &__title {
      font-size: 30px;
      letter-spacing: -1.5px;
    }
    &__descr {
      max-width: 635px;
      padding: 0;
      margin: 0 0 45px;
      height: 92px;
    }
  }
}

@media screen and (min-width: 1200px) {
  .processes-screen {
    &__bg {
      display: block;
      position: absolute;
      top: 0;
      left: 50%;
      width: 100%;
      height: 100%;
      transform: translate(-50%, 0);
      background-repeat: no-repeat;
      background-position: center right;
    }
  }
}