.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  border-radius: 25px;
  background-color: transparent;
  border: 2px solid #0079c4;
  height: 44px;
  padding: 0 25px;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s ease;
}
.btn:hover {
  background-color: #0079c4;
}
.btn:hover .btn__text {
  color: white;
}
.btn__text {
  color: #0079c4;
  font-size: 12px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  line-height: 1.12;
  letter-spacing: 0.3px;
  margin: 0 5px;
  transition: color .3s ease;
}
.btn_white .btn__text {
  color: white;
}
.btn_text-white .btn__text {
  color: white;
}
.btn_white {
  border-color: white;
}
.btn_white:hover {
  background-color: transparent;
}
.btn_white:hover .btn__text {
  color: white;
}
.btn_icon {
  padding: 0 18px;
}
.btn_icon .icon {
  display: block;
  margin: 0 5px;
  transition: background .3s ease;
}
.btn_icon .icon.icon-phone {
  background: svg('icon-phone', '[fill]: #ffffff') no-repeat 0 0;
  background-size: 12px 12px;
  width: 12px;
  height: 12px;
}
.btn_icon .icon.icon-arrow {
  background: svg('icon-arrow') no-repeat 0 0;
  background-size: 16px 7px;
  width: 16px;
  height: 7px;
}
.btn_icon-blue .icon.icon-phone {
  background: svg('icon-phone', '[fill]: #0079c4') no-repeat 0 0;
  background-size: 12px 12px;
  width: 12px;
  height: 12px;
}
.btn_icon-blue:hover .icon.icon-phone {
  background: svg('icon-phone', '[fill]: #ffffff') no-repeat 0 0;
  background-size: 12px 12px;
  width: 12px;
  height: 12px;
}
.btn_footer {
  border-color: rgba(0, 121, 196, 0.1);
  transition: border-color 0.3s ease;
}
.btn_footer .btn__text {
  color: #0079c4;
}
.btn_footer:hover {
  border-color: #0079c4;
  background-color: transparent;
}
.btn_footer:hover .btn__text {
  color: #0079c4;
}
.btn_footer:hover .icon.icon-phone {
  background: svg('icon-phone', '[fill]: #0079c4') no-repeat 0 0;
  background-size: 12px 12px;
  width: 12px;
  height: 12px;
}
.btn_short {
