  margin-bottom: 20px;
}
.menu__phone_bold {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  margin-bottom: 0;
}
.menu__body {
  flex-grow: 20;
  height: 100%;
}
.menu__body-inner {
  width: 100%;
  height: 100%;
}
.menu__body-scroll {
  padding: 30px 15px;
}
.menu__footer {
  padding-bottom: 40px;
}
@keyframes menuShow {
  0% {
    visibility: hidden;
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
    visibility: visible;
  }
}
@keyframes menuHide {
  0% {
    opacity: 1;
    transform: translateX(0);
    visibility: visible;
  }
  100% {
    visibility: hidden;
    opacity: 0;
    transform: translateX(100%);
  }
}
@media screen and (min-width: 768px) {
  .menu__head {
    padding: 15px 40px;
  }
  .menu__contacts {
    flex-direction: row;
  }
  .menu__head .menu__contacts {
    display: flex;
  }
  .menu__footer .menu__contacts {
    display: none;
  }
  .menu__contacts .btn {
    margin-left: 20px;
  }
  .menu__phone {
    margin-bottom: 0;
  }
  .menu__body-scroll {
    padding: 30px 15px;
  }
  .menu__footer {
    display: none;
  }
}
@media screen and (min-width: 1000px) {
  .menu {
    height: 100%;
    width: 45%;
    max-width: 630px;
  }
  .menu__head {
    padding: 20px 40px;
    align-items: flex-start;
    border-bottom: 0;
  }
  .menu__logo-link {
    display: none;
  }
  .menu__contacts {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .menu__contacts .btn {
    margin-left: 0;
  }
  .menu__phone {
    font-size: 18px;
    margin-bottom: 15px;
  }
  .menu__phone_bold {
    margin-bottom: 0;
  }
  .menu__body {
    flex-grow: 20;
    min-height: 250px;
    height: 100%;
    overflow: hidden;
  }
  .menu__body-inner {
    width: calc(100% + 50px);
    height: 100%;
    overflow-y: scroll;
  }
  .menu__body-scroll {
    padding: 20px 40px;
  }
  .menu__footer {
    flex-shrink: 0;
    position: relative;
    display: block;
    padding: 30px 40px 20px;
  }
  .menu__footer:before {
    content: "";
    position: absolute;
    top: 0;
    left: 40px;
    width: calc(100% - 80px);
    height: 6px;
    border-radius: 3px;
    background-color: #e9f0f5;
  }
}
@media screen and (min-width: 1200px) {
  .menu__contacts {
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .menu__contacts .btn {
    margin-left: 20px;
  }
  .menu__phone {
    margin-bottom: 0;
  }
}
@media screen and (min-width: 1350px) {
  .menu__head {
    padding: 20px 40px 20px 80px;
  }
  .menu__body-scroll {
    padding: 85px 40px 85px 80px;
  }
  .menu__phone {
    font-size: 20px;
  }
  .menu__footer {
    padding: 85px 40px 85px 80px;
  }
  .menu__footer:before {
    left: 80px;
    width: calc(100% - (80px + 50px));
  }
}
@media (max-height: 600px) and (min-width: 1350px) {
  .menu__body-scroll {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .menu__footer {
    padding-top: 30px;
    padding-bottom: 20px;
  }
}
.menu-nav__list {
  text-align: center;
}
.menu-nav__item {
  margin-bottom: 5px;
}
.menu-nav__item:last-child {
  margin-bottom: 0;
}
.menu-nav__link {
  text-decoration: none;
  color: #001628;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.25;
  padding: 10px 0;
  display: inline-block;
  transition: color .3s ease;
  letter-spacing: 0.28px;
  text-align: center;
}
.menu-nav__link:hover {
  color: #0079c4;
}
@media screen and (min-width: 1000px) {
  .menu-nav__list {
    text-align: left;
  }
  .menu-nav__item {
    margin-bottom: 27px;
  }
  .menu-nav__link {
    font-size: 20px;
  }
}
