.fancybox-bg {
  background: rgba(0,0,0,.4);
}

.fancybox-is-open .fancybox-bg {
  opacity: 1;
  background: rgba(0,0,0,.4);
}
button[data-fancybox-close] {
  display: none;
}

.fancybox-menu-active {
  .fancybox-infobar,
  .fancybox-toolbar,
  .fancybox-navigation,
  .fancybox-caption-wrap {
    display: none;
  }
  .fancybox-slide > * {
    padding: 0;
    margin: 0;
    border-width: 0;
    vertical-align: top;
    overflow: auto;
  }
  .fancybox-bg {
    background-color: rgba(0, 22, 40, .8);
  }

  outline: none;
  &:focus {
    outline: none;
  }
}