.hero {
  width: 100%;
  height: 565px;
  position: relative;
  &__bg-wrap {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(0,0,0,.4);
      z-index: 2;
    }
  }
  &__bg-img-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
  &__bg-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    vertical-align: middle;
  }
  &__content {
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 28px 0;
  }
  &__title {
    text-align: center;
    color: #ffffff;
    .light();
    font-size: 26px;
    line-height: 1.38;
    max-width: 260px;
  }
  &__title-hl {
    .bold();
  }
  &__bottom {
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 50px;
    left: 0;
    z-index: 1;
  }
}

@media screen and (min-width: 768px) {
  .hero {
    min-height: 500px;
    height: 100vh;
    &__bottom {
      text-align: left;
    }
    &__title {
      font-size: 32px;
      line-height: 1.41;
      max-width: 480px;
    }
  }
}

@media screen and (min-width: 1000px) {
  .hero {
    &__title {
      font-size: 36px;
      line-height: 1.53;
      max-width: none;
    }
    &__title-hl {
      display: block;
    }
  }
}