.presentation {
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  &__icon-wrap {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    border: 10px solid rgba(255,255,255,.2);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 16px;
  }
  &__text-wrap {
    text-align: left;
  }
  &__link {
    text-decoration: none;

    .icon-pdf {
      display: block;
      .svg-icon(icon-pdf, 23px, 31px);
    }
  }
  &__title {
    color: #ffffff;
    .semi-bold();
    font-size: 16px;
    line-height: 1.13;
    text-transform: uppercase;
    margin-bottom: 2px;
  }
  &__descr {
    color: #ffffff;
    .italic();
    font-size: 12px;
    font-style: italic;
    line-height: 1.5;
    letter-spacing: 0.3px;
    max-width: 145px;
  }
}