.tabs {
  &__menu-wrap {
    margin: 0 -15px;
  }
  &__menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    max-width: 354px;
  }
  &__menu-item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 108px;
    height: 108px;
    margin: 5px;
    cursor: pointer;
    text-align: center;
    background-color: rgba(23, 56, 82, .2);
    border-radius: 10px;
    transition: background-color .3s ease;

    .icon {
      display: block;
      margin: 0 auto 12px;
      &.icon-process {
        .svg-icon(icon-process, 38px, 38px);
      }
      &.icon-project {
        .svg-icon(icon-project, 38px, 38px);
      }
      &.icon-control {
        .svg-icon(icon-control, 38px, 38px);
      }
      &.icon-diagnostic {
        .svg-icon(icon-diagnostic, 38px, 38px);
      }
      &.icon-note {
        .svg-icon(icon-note, 38px, 38px);
      }
      &.icon-support {
        .svg-icon(icon-support, 38px, 38px);
      }
      transition: background .3s ease;
    }

    &_current, &:hover {
      background-color: #1d4362;

      .tabs__menu-item-name {
        color: white;
      }
      .icon {
        &.icon-process {
          .svg-icon-param(icon-process, 38px, 38px, "[fill]: #ffffff");
        }
        &.icon-project {
          .svg-icon-param(icon-project, 38px, 38px, "[fill]: #ffffff");
        }
        &.icon-control {
          .svg-icon-param(icon-control, 38px, 38px, "[fill]: #ffffff");
        }
        &.icon-diagnostic {
          .svg-icon-param(icon-diagnostic, 38px, 38px, "[fill]: #ffffff");
        }
        &.icon-note {
          .svg-icon-param(icon-note, 38px, 38px, "[fill]: #ffffff");
        }
        &.icon-support {
          .svg-icon-param(icon-support, 38px, 38px, "[fill]: #ffffff");
        }
      }
    }
  }
  &__menu-item-name {
    color: #264967;
    .bold();
    font-size: 10px;
    line-height: 1.12;
    text-transform: uppercase;
    transition: color .3s ease;
  }
}

@media screen and (min-width: 768px) {
  .tabs {
    &__menu {
      max-width: none;
    }
  }
}

@media screen and (min-width: 1000px) {
  .tabs {
    &__menu-wrap {
      margin: 0 -10px;
    }
    &__menu {
      justify-content: flex-start;
    }
    &__menu-item {
      width: 124px;
      height: 124px;
      margin: 0 10px;

      .icon {
        &.icon-process {
          .svg-icon(icon-process, 46px, 46px);
        }
        &.icon-project {
          .svg-icon(icon-project, 46px, 46px);
        }
        &.icon-control {
          .svg-icon(icon-control, 46px, 46px);
        }
        &.icon-diagnostic {
          .svg-icon(icon-diagnostic, 46px, 46px);
        }
        &.icon-note {
          .svg-icon(icon-note, 46px, 46px);
        }
        &.icon-support {
          .svg-icon(icon-support, 46px, 46px);
        }
      }

      &_current, &:hover {
        .icon {
          &.icon-process {
            .svg-icon-param(icon-process, 46px, 46px, "[fill]: #ffffff");
          }
          &.icon-project {
            .svg-icon-param(icon-project, 46px, 46px, "[fill]: #ffffff");
          }
          &.icon-control {
            .svg-icon-param(icon-control, 46px, 46px, "[fill]: #ffffff");
          }
          &.icon-diagnostic {
            .svg-icon-param(icon-diagnostic, 46px, 46px, "[fill]: #ffffff");
          }
          &.icon-note {
            .svg-icon-param(icon-note, 46px, 46px, "[fill]: #ffffff");
          }
          &.icon-support {
            .svg-icon-param(icon-support, 46px, 46px, "[fill]: #ffffff");
          }
        }
      }
    }
  }
}