.footer-nav {
  display: none;
}

@media screen and (min-width: 768px) {
  .footer-nav {
    display: block;
    &__list {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
    }
    &__item {
      margin-right: 45px;
      &:last-child {
        margin-right: 0;
      }
    }
    &__link {
      color: #0079c4;
      .medium();
      font-style: italic;
      font-size: 12px;
      text-decoration: none;
      transition: color .3s ease;
      &:hover {
        color: white;
      }
    }
  }
}


@media screen and (min-width: 1000px) {
  .footer-nav {
    &__link {
      font-size: 14px;
    }
  }
}

@media screen and (min-width: 1200px) {
  .footer-nav {
    &__item {
      margin-right: 55px;
    }
  }
}