.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  border-radius: 25px;
  background-color: transparent;
  border: 2px solid @blue;
  height: 44px;
  padding: 0 25px;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
  transition: background-color .3s ease;

  &:hover {
    background-color: @blue;
    .btn__text {
      color: white;
    }
  }

  &__text {
    color: @blue;
    font-size: 12px;
    .medium();
    line-height: 1.12;
    letter-spacing: 0.3px;
    margin: 0 5px;
    transition: color .3s ease;

    .btn_white & {
      color: white;
    }
    .btn_text-white & {
      color: white;
    }
  }

  &_white {
    border-color: white;
    &:hover {
      background-color: transparent;
      .btn__text {
        color: white;
      }
    }
  }

  &_icon {
    padding: 0 18px;
    .icon {
      display: block;
      margin: 0 5px;
      transition: background .3s ease;

      &.icon-phone {
        .svg-icon-param(icon-phone, 12px, 12px, "[fill]: #ffffff");
      }
      &.icon-arrow {
        .svg-icon(icon-arrow, 16px, 7px);
      }
    }
  }

  &_icon-blue {
    .icon {
      &.icon-phone {
        .svg-icon-param(icon-phone, 12px, 12px, "[fill]: @{blue}");
      }
    }
    &:hover {
      .icon {
        &.icon-phone {
          .svg-icon-param(icon-phone, 12px, 12px, "[fill]: #ffffff");
        }
      }
    }
  }

  &_footer {
    border-color: rgba(0, 121, 196, 0.1);
    transition: border-color .3s ease;
    .btn__text {
      color: @blue;
    }
    &:hover {
      border-color: @blue;
      background-color: transparent;
      .btn__text {
        color: @blue;
      }
      .icon {
        &.icon-phone {
          .svg-icon-param(icon-phone, 12px, 12px, "[fill]: @{blue}");
        }
      }
    }
  }

  &_short {
    padding: 0 50px;
  }
}