.social-menu {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;

  .footer & {
    margin-bottom: 30px;
  }

  .menu & {
    display: none;
  }

  &__item {
    margin: 0 4px;
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border: 2px solid rgba(0, 121, 196, 0.1);
    transition: border-color .3s ease;
    &:hover {
      border-color: @blue;
    }
  }

  .icon {
    display: block;
    &.icon-linkedin {
      .svg-icon(icon-linkedin, 14px, 14px);
    }
    &.icon-fb {
      .svg-icon(icon-fb, 14px, 14px);
    }
    &.icon-tw {
      .svg-icon(icon-tw, 14px, 14px);
    }
  }
}

@media screen and (min-width: 768px) {
  .social-menu {
    .footer & {
      margin-bottom: 0;
    }
  }
}

@media screen and (min-width: 1000px) {
  .social-menu {

    .menu & {
      display: flex;
    }

    &__link {
      width: 44px;
      height: 44px;
    }
  }
}