.form {
  &__field {
    margin-bottom: 10px;
    &_btn-wrap {
      margin: 15px 0;
    }

    .btn {
      width: 100%;
    }
  }
  &__input-cover {}
  &__input {
    border-radius: 25px;
    border: 0;
    width: 100%;
    height: 42px;
    background-color: @gray;
    padding: 0 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    color: @dark-blue;
    .regular();
    font-size: 14px;
    transition: background-color .3s ease;
    &:hover, &:focus {
      background-color: #f3f6f8;
    }

    &::placeholder {
      color: rgba(0, 22, 40, .5);
      .regular();
      font-size: 14px;
    }

    &_error {
      background-color: #ff8686;
    }
  }
  &__bottom {}
  &__privacy-text {
    text-align: center;
    color: @dark-blue;
    .medium();
    font-style: italic;
    font-size: 12px;
    line-height: 1.33;
  }
  &__privacy-link {
    text-decoration: none;
    color: @blue;
  }
}

@media screen and (min-width: 768px) {
  .form {
    &__privacy-text {
      text-align: right;
    }
  }
}.form {}
