.header {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  width: 100%;
  z-index: 5;
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__logo-link {
    text-decoration: none;
  }
  &__logo {
    width: 124px;
    vertical-align: middle;
    margin-bottom: 14px;
  }
  &__logo-title {
    color: #ffffff;
    .medium();
    font-size: 12px;
    line-height: 1.5;
    letter-spacing: 0.3px;
    max-width: 225px;
  }
  &__contacts {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .header {
    padding: 18px 0;
    &__col {
      &_left {}
      &_right {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      }
    }
    &__contacts {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-wrap: nowrap;
      margin-right: 28px;
    }
    &__phone {
      text-decoration: none;
      color: #ffffff;
      .light();
      font-size: 16px;
      line-height: 1.12;
      margin-right: 20px;
      &_bold {
        .bold();
        margin-right: 0;
      }
    }
  }
}

@media screen and (min-width: 1000px) {
  .header {
    padding: 23px 0;
    &__contacts {
      margin-right: 60px;
    }
    &__phone {
      .light();
      font-size: 20px;
      &_bold {
        .bold();
      }
    }
  }
}