.advantages-screen {
  &__body {
    position: relative;
    padding: 55px 0;
    overflow: hidden;
  }
  &__bg-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(0,0,0,.4);
      z-index: 2;
    }
  }
  &__bg-img-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
  &__bg-img {
    vertical-align: middle;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__container {
    position: relative;
    z-index: 2;
  }
  &__title {
    text-align: center;
    color: #ffffff;
    .light();
    font-size: 26px;
    max-width: 640px;
    margin: 0 auto 75px;
  }
  &__title-hl {
    .bold();
  }
  &__footer {
    padding: 45px 0;
  }
  &__footer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__stat-box {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__stat-number {
    color: @blue;
    .light();
    font-size: 38px;
    line-height: 1.32;
  }
  &__stat-info {
    color: @dark-blue;
    .regular();
    font-size: 14px;
    line-height: 1.43;
    width: 160px;
  }
}

@media screen and (min-width: 375px) {
  .advantages-screen {
    &__stat-box {
      justify-content: center;
    }
    &__stat-number {
      margin-right: 30px;
    }
  }
}

@media screen and (min-width: 768px) {
  .advantages-screen {
    &__body {
      padding: 65px 0;
    }
    &__title {
      font-size: 28px;
      line-height: 1.43;
      margin-bottom: 55px;
    }
    &__footer {
      padding: 55px 0;
    }
    &__footer-container {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
    &__stat-box {
      justify-content: flex-start;
      margin-bottom: 0;
      &:nth-child(2n) {
        justify-content: flex-end;
      }
    }
  }
}

@media screen and (min-width: 850px) {
  .advantages-screen {
    &__footer-container {
      justify-content: space-around;
    }
    &__stat-box {
      justify-content: center;
      &:nth-child(2n) {
        justify-content: center;
      }
    }
  }
}

@media screen and (min-width: 1000px) {
  .advantages-screen {
    &__body {
      padding: 140px 0 85px;
    }
    &__title {
      text-align: left;
      font-size: 30px;
      line-height: 1.67;
      margin-bottom: 60px;
      margin-left: 0;
    }
    &__stat-number {
      color: @blue;
      .light();
      font-size: 48px;
      line-height: 1.04;
    }
  }
}