.footer {
  background-color: @dark-blue;
  padding: 40px 0;
  &__top {}
  &__middle {
    display: none;
  }
  &__bottom {}

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    &_top {}
    &_middle {}
    &_bottom {}
  }

  &__contacts-wrap {
    margin-bottom: 30px;

    .btn {
      margin-bottom: 20px;
    }
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__phone {
    text-decoration: none;
    color: white;
    .light();
    font-size: 18px;
    margin-bottom: 5px;
    &_bold {
      .bold();
    }
  }
  &__email {
    text-decoration: none;
    color: @blue;
    .bold();
    font-style: italic;
    font-size: 12px;
    font-weight: 400;
    transition: color .3s ease;
    &:hover {
      text-decoration: underline;
      color: #0889d8;
    }
  }

  &__copyright-wrap {
    text-align: center;
    margin-bottom: 30px;
  }
  &__copyright {
    color: #ffffff;
    .semi-bold();
    font-size: 14px;
    text-transform: uppercase;
    margin-bottom: 25px;
  }
  &__privacy {
    color: @blue;
    .medium();
    font-style: italic;
    font-size: 12px;
    line-height: 1.12;
    text-decoration: underline;
    transition: color .3s ease;
    &:hover {
      text-decoration: none;
      color: #0889d8;
    }
  }
  &__bottom-title {
    color: @blue;
    .light();
    font-size: 16px;
    line-height: 1.2;
    letter-spacing: 0.18px;
    margin-bottom: 25px;
    text-align: center;
  }
  &__developers {
    color: #ffffff;
    .regular();
    font-size: 12px;
    line-height: 1.12;
    text-align: center;
  }
  &__developers-link {
    color: @blue;
    .semi-bold();
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
}

@media screen and (min-width: 768px) {
  .footer {
    padding: 35px 0;
    &__top {
      margin-bottom: 30px;
    }
    &__middle {
      display: block;
      margin-bottom: 45px;
    }

    &__container {
      &_top {
        justify-content: space-between;
        flex-direction: row;
      }
      &_middle {
        flex-direction: row;
        justify-content: space-between;
      }
      &_bottom {
        flex-direction: row;
        justify-content: space-between;
      }
    }

    &__contacts-wrap {
      margin-bottom: 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;

      .btn {
        margin-bottom: 0;
        margin-right: 30px;
      }
      .footer__top & {
        display: none;
      }
    }

    &__copyright-wrap {
      .footer__middle & {
        margin-bottom: 0;
      }
      .footer__bottom & {
        margin-bottom: 0;
      }
    }
    &__copyright {
      .footer__middle & {
        margin-bottom: 0;
      }
      .footer__bottom & {
        display: none;
      }
    }

    &__bottom-title {
      .footer__bottom & {
        margin-bottom: 0;
      }
    }
  }
}

@media screen and (min-width: 1000px) {
  .footer {
    &__middle {
      display: none;
    }

    &__container {
      &_top {
        align-items: flex-start;
      }
      &_bottom {
        flex-direction: row;
        justify-content: space-between;
      }
    }

    &__contacts-wrap {
      .btn {
        margin-bottom: 20px;
        margin-right: 0;
      }
      .footer__top & {
        display: flex;
        flex-direction: column;
      }
    }
    &__contacts {
      align-items: flex-end;
    }

    &__copyright-wrap {
      .footer__bottom & {
        margin-bottom: 0;
      }
    }
    &__copyright {
      .footer__bottom & {
        display: inline-block;
        margin-bottom: 0;
        vertical-align: middle;
        margin-right: 19px;
      }
    }
    &__privacy {
      font-size: 13px;
    }

    &__bottom-title {
      font-size: 24px;
      letter-spacing: 0.28px;
    }

    &__developers {
      font-size: 13px;
    }
  }
}

@media screen and (min-width: 1200px) {
  .footer {
    padding: 40px 0 30px;
    &__container {
      &_top {
        align-items: center;
      }
    }

    &__contacts-wrap {
      .btn {
        margin-bottom: 0;
        margin-right: 48px;
      }
      .footer__top & {
        flex-direction: row;
      }
    }
  }
}