body {
  -webkit-font-smoothing: antialiased;
  min-width: 320px;
  color: @dark-blue;
  text-align: left;
  .regular();
  font-size: 16px;
  line-height: 1.2;
  position: relative;
  opacity: 0;
  transition: opacity .3s ease;
  &.loaded {
    opacity: 1;
  }

  &.menu-active {
    overflow: hidden;
  }
}

.container {
  max-width: 1235px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 768px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (min-width: 1000px) {
  .container {
    max-width: 1240px;
    padding-left: 20px;
    padding-right: 20px;

    &_wide {
      max-width: 100%;
      padding: 0 40px;
    }
  }
}