.advices-screen {
  padding: 50px 0 20px;
  &__container {}
  &__title {
    text-align: center;
    color: @dark-blue;
    .light();
    font-size: 26px;
    line-height: 1.2;
    margin-bottom: 25px;
  }
  &__title-hl {
    .bold();
  }
  &__slider-wrap {
    position: relative;
  }
  &__slider-nav-wrap {
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    right: 16px;
    bottom: 34px;
    z-index: 1;
  }
  &__slider-nav {
    width: 38px;
    height: 38px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid @blue;
    margin: 4px;
    cursor: pointer;
    transition: border-color .3s ease, background-color .3s ease;
    &:hover {
      background-color: @blue;
      border-color: @blue;

      .icon {
        &.icon-prev {
          .svg-icon-param(icon-prev, 6px, 8px, "[fill]: #ffffff");
        }
        &.icon-next {
          .svg-icon-param(icon-next, 6px, 8px, "[fill]: #ffffff");
        }
      }
    }

    &.swiper-button-disabled {
      border-color: #cbd7df;
      .icon {
        &.icon-prev {
          .svg-icon-param(icon-prev, 6px, 8px, "[fill]: #cbd7df");
        }
        &.icon-next {
          .svg-icon-param(icon-next, 6px, 8px, "[fill]: #cbd7df");
        }
      }

      &:hover {
        border-color: #cbd7df;
        background-color: transparent;
      }
    }

    .icon {
      display: block;
      transition: background .3s ease;
      &.icon-prev {
        .svg-icon-param(icon-prev, 6px, 8px, "[fill]: @{blue}");
      }
      &.icon-next {
        .svg-icon-param(icon-next, 6px, 8px, "[fill]: @{blue}");
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .advices-screen {
    padding: 55px 0 30px;
    &__container {}
    &__title {
      text-align: left;
      font-size: 28px;
      margin-bottom: 35px;
      width: calc(~"100% - 130px");
    }
    &__slider-nav-wrap {
      right: 0;
      bottom: calc(~"100% + 26px");
    }
  }
}

@media screen and (min-width: 1200px) {
  .advices-screen {
    padding: 84px 0 40px;
    &__title {
      font-size: 30px;
      margin-bottom: 45px;
    }
    &__slider-nav-wrap {
      bottom: calc(~"100% + 34px");
    }
    &__slider-nav {
      width: 46px;
      height: 46px;
      margin: 5px;
      &:hover {
        .icon {
          &.icon-prev {
            .svg-icon-param(icon-prev, 7px, 10px, "[fill]: #ffffff");
          }
          &.icon-next {
            .svg-icon-param(icon-next, 7px, 10px, "[fill]: #ffffff");
          }
        }
      }

      &.swiper-button-disabled {
        .icon {
          &.icon-prev {
            .svg-icon-param(icon-prev, 7px, 10px, "[fill]: #cbd7df");
          }
          &.icon-next {
            .svg-icon-param(icon-next, 7px, 10px, "[fill]: #cbd7df");
          }
        }
      }

      .icon {
        &.icon-prev {
          .svg-icon-param(icon-prev, 7px, 10px, "[fill]: @{blue}");
        }
        &.icon-next {
          .svg-icon-param(icon-next, 7px, 10px, "[fill]: @{blue}");
        }
      }
    }
  }
}