.menu-nav {
  &__list {
    text-align: center;
  }
  &__item {
    margin-bottom: 5px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__link {
    text-decoration: none;
    color: #001628;
    .light();
    font-size: 14px;
    line-height: 1.25;
    padding: 10px 0;
    display: inline-block;
    transition: color .3s ease;
    letter-spacing: 0.28px;
    text-align: center;
    &:hover {
      color: @blue;
    }
  }
}

@media screen and (min-width: 1000px) {
  .menu-nav {
    &__list {
      text-align: left;
    }
    &__item {
      margin-bottom: 27px;
    }
    &__link {
      font-size: 20px;
    }
  }
}